.home .title {
  font-weight: bold;
  font-size: 1.5em;
  background-image: -webkit-gradient(linear, left top, right top, color-stop(20%, blue), color-stop(purple), color-stop(80%, lime));
  background-image: -webkit-linear-gradient(left, blue 20%, purple, lime 80%);
  background-image: -o-linear-gradient(left, blue 20%, purple, lime 80%);
  background-image: linear-gradient(to right, blue 20%, purple, lime 80%); 
  color: purple;
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  padding: 10px 0;
  margin: 0;
}


